<!--新建/编辑直播-->
<template>
  <div class="live-info">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>直播设置</el-breadcrumb-item>
      <el-breadcrumb-item>新建直播</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="container">
      <el-form ref="Form" :rules="rules" :model="form" label-width="90px" style="width:565px">
        <el-form-item label="直播名称:" prop="liveRoomName">
          <el-input v-model="form.liveRoomName" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item label="时间:" prop="time">
          <div class="flex-between">
            <el-date-picker
              style="width:100%"
              v-model="form.time"
              type="datetimerange"
              range-separator="至"
              start-placeholder="请选择开始日期"
              end-placeholder="请选择结束日期">
            </el-date-picker>
            <!-- <el-time-picker v-model="form.startTime" placeholder="请选择开始日期"></el-time-picker>
            <el-time-picker arrow-control v-model="form.endTime" placeholder="请选择结束日期"></el-time-picker> -->
          </div>
        </el-form-item>
        <el-form-item label="直播机构:" prop="orgid">
          <el-select @change="handleOrigidChange" v-model="form.orgid" style="width:100%">
            <el-option v-for="item in orgidList" :key="item.id" :value="item.id" :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="直播人:">
          <el-select v-model="form.liveUserId" style="width:100%">
            <el-option v-for="item in liveUserIdList" :key="item.key" :value="item.id" :label="item.name"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="简介:">
          <el-input v-model="form.remark" type="textarea" :rows="4" resize="none"></el-input>
        </el-form-item>
        <div class="el-form-item arrow">
          <label style="width:100px" class="el-form-item__label">
            <span>
            高级选项 
            <i @click="showArrow = !showArrow" :class="['iconfont','icon-arrow-down',{active:showArrow}]"></i></span>
          </label>
        </div>
        <el-form-item v-if="showArrow" label="访问权限:">
          <div class="flex-between">
            <el-select v-model="form.lookPower"  style="width:48%">
              <el-option :value="9060" label="公开"></el-option>
              <el-option :value="9061" label="凭密码访问"></el-option>
              <el-option :value="9062" label="指定机构查看"></el-option>
            </el-select>
            <el-input v-if="form.lookPower===9061" style="width:48%;" v-model="form.lookPassWord" placeholder="请输入密码"></el-input>
            <el-input @focus="handleFocus" v-if="form.lookPower===9062" v-model="transferPlace" suffix-icon="el-icon-arrow-down" style="width:48%;" placeholder="请选择机构"></el-input>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button  @click="submitForm" type="primary">保存</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- transfer 穿梭框 -->
    <div class="transfer">
      <el-transfer
        filterable
        v-model="transferValue"
        filter-placeholder="请输机构名筛选"
        :data="transferData"
        :filter-method="filterMethod"
      >
      </el-transfer>
    </div>

  </div>
</template>

<script>
import {dateFormat} from '@/assets/javascript/ultis'
export default {
  props:['id'],
  data() {
    return {
      showArrow:false,
      form:{
        liveRoomName:'',
        time:'',
        orgid:'',
        liveUserId:'',
        remark:'',
        lookPower: 9060,
        lookPassWord:'',
        orgIds:''
      },
      rules:{
        liveRoomName:[ { required: true, message: '请输入直播名称', trigger: 'blur' }],
        time:[ { required: true, message: '请选择时间',trigger: 'change'}],
        orgid:[ { required: true, message: '请选择直播机构',trigger: 'change'}]
      },
      /* 直播机构lists */
      orgidList:[],
      /* 直播人lists */
      liveUserIdList:[],
      /* transfer */
      filterMethod(query, item) {
        return item.label.indexOf(query) > -1;
      },
      transferValue:[],
      transferLength:0,
      /* 创建 */
      createType:'',
      appointmentType:''
    };
  },
  computed:{
    transferData(){
      return this.orgidList.map(item => ({
        label: item.name,
        key: item.id,
      }))
    },
    transferPlace(){
      return this.transferLength !== 0 ? `${this.transferLength}个` : '请选择机构'
    }
  },
  created(){
    this.getAllOrg();
    /* 编辑 */
    this.id && this.getLiveRoom()
  },
  methods:{
    /* 获取编辑信息 */
    getLiveRoom() {
      this.showArrow = true
      this.handleOrigidChange(this.id)
      const params ={liveRoomId:this.id}
      this.$api.getLiveroomDetail(params).then(res => {
        this.createType = res.data.createType
        this.appointmentType = res.data.appointmentType
        this.form.liveRoomName = res.data.liveRoomName
        this.form.orgid = res.data.orgid
        this.form.liveUserId = res.data.liveUserId
        this.form.remark = res.data.remark
        this.form.lookPower = res.data.lookPower
        this.form.time = [new Date(res.data.startTime),new Date(res.data.endTime)]
        this.form.lookPassWord = res.data.lookPassWord
        this.form.orgIds = res.data.orgIds
        if(res.data.orgIds) {
          this.transferValue = [...res.data.orgIds.split(",")]
        }
        this.transferLength = this.transferValue.length
      })
    },
    /* 获取直播机构 */
    getAllOrg(){
      this.$api.queryAllOrg().then(res => {
        this.orgidList = res.data;
      })
    },
    /* 获取直播人通过直播机构 */
    handleOrigidChange(orgId){
      const params = {orgId}
      this.$api.queryUserByOrg(params).then(res => {
        this.liveUserIdList = res.data;
      })
    },

    /* 提交 */
    submitForm(){
      this.$refs.Form.validate(valid => {
        if(valid) {
          /* ajax */
          const params = {
            liveRoomName:this.form.liveRoomName,
            startTime:dateFormat(this.form.time[0],"yyyy-MM-dd hh:mm:ss"),
            endTime:dateFormat(this.form.time[1],"yyyy-MM-dd hh:mm:ss"),
            orgid: this.form.orgid,
            remark:this.form.remark,
            lookPower:this.form.lookPower,
            lookPassWord:this.form.lookPassWord,
            orgIds: this.form.orgIds,
            createType:9071,
            appointmentType:8004
          }
          if(this.id) { 
            params.liveRoomId = this.id;
            params.createType = this.createType;
            params.appointmentType = this.appointmentType
          }
          this.$api.createOrUpdateLiveRoom(params).then(res => {            
            this.$myLayer.successLayer(this.id ? "编辑成功" : "添加成功",
              () => {
              this.$router.push('/liveCenter/Layout/liveSetting')
            })
          })
        }
      })
    },

    /* 弹出formLayer */
    handleFocus(){
      if(this.form.orgIds) {
        this.transferValue = [...this.form.orgIds.split(',')]
      }
      this.$myLayer.formLayer('指定机构查看',$('.transfer'),['749px','536px'],() => {
        this.transferLength = this.transferValue.length;
        this.form.orgIds = this.transferValue.join(",")
      })
    }
  }
};
</script>

<style lang="scss" scoped>
.live-info {
  padding: 32px;
  min-height: 100%;
  height: 100%;
}
.container {
  margin-top: 23px;
  height: calc(100% - 37px);
  background: #fff;
  border-radius: 6px;
  padding: 24px;
}
.arrow {
  i {
  transform: rotate(90deg);
  display: inline-block;
  position: relative;
  top: 1px;cursor: pointer;
  &.active {transform: rotate(-90deg)}
  }
}

.transfer {
  width: 100%;
  height: 100%;
  display: none;
  padding: 30px;
}
</style>
<style>
.live-info .el-button--primary span {color: #fff}
</style>

<style>
  /* transfer */
  .transfer .el-transfer {
    height: 100%;
    display: flex;
    align-self: center;
  }
  .transfer .el-transfer > div {}
  .transfer .el-transfer .el-transfer-panel {
    flex:1
  }
  .transfer .el-transfer-panel__header {
    display: none
  }
  .transfer .el-transfer-panel__body {
    height: 100%;
  }
  .transfer .el-transfer-panel__list.is-filterable {
    height: 100%;
  }

  .transfer .el-transfer-panel__filter {
    margin: 0
  }

  .transfer .el-transfer-panel__filter .el-input__inner {
    border-radius: 0;
    border: none;
    border-bottom: 1px solid #E5E5E5;
  }

  .transfer .el-input__prefix {
    left: 160px;
  }

  .transfer .el-transfer__buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;;
  }
  .transfer .el-transfer__button {
    border-radius: 4px;
    width: 45px;
    height: 30px;
    line-height: 30px;
    padding: 0;

  }

  .transfer .el-transfer__button span i {
    color: #fff
  }

  .transfer .el-transfer__button.is-disabled span i {
    color: #b5b5b5
  }
</style>